import React from 'react';
import { Link } from 'react-router-dom';
import './Paginate.css';

/**
 * Paginate Bileeni
 * Sayfalama arayzn oluturur.
 * ki modda alabilir:
 * 1. URL Deitirme Modu (varsaylan): react-router-dom Link'leri oluturur.
 * 2. Fonksiyon Tetikleme Modu: Eer 'onPageChange' prop'u verilirse, Link yerine buton oluturur ve bu fonksiyonu arr.
 * * @param {object} props
 * @param {number} props.pages - Toplam sayfa says
 * @param {number} props.page - Aktif sayfa numaras
 * @param {function} [props.onPageChange] - Sayfa deitiinde arlacak fonksiyon (opsiyonel)
 * @param {boolean} [props.isAdmin] - Admin paneli URL yaps iin flag
 * @param {string} [props.keyword] - Arama kelimesi (URL iin)
 * @param {string} [props.baseUrl] - Admin URL'leri iin temel yol
 */
const Paginate = ({ pages, page, isAdmin = false, keyword = '', baseUrl = '/products', onPageChange }) => {
  if (pages <= 1) {
    return null;
  }

  // URL tabanl sayfalama iin link oluturan fonksiyon
  const getPageUrl = (x) => {
    const pageNum = x + 1;
    if (isAdmin) {
      return `${baseUrl}/page/${pageNum}`;
    } else {
      if (keyword) {
        return `/search/${keyword}/page/${pageNum}`;
      }
      return `/products/page/${pageNum}`;
    }
  };

  return (
    <div className="pagination">
      {[...Array(pages).keys()].map((x) => {
        const pageNum = x + 1;
        // onPageChange prop'u varsa, Link yerine button render et
        if (onPageChange) {
          return (
            <button
              key={pageNum}
              onClick={() => onPageChange(pageNum)}
              className={pageNum === page ? 'page-item active' : 'page-item'}
            >
              {pageNum}
            </button>
          );
        }

        // onPageChange yoksa, standart Link'i render et
        return (
          <Link
            key={pageNum}
            to={getPageUrl(x)}
            className={pageNum === page ? 'page-item active' : 'page-item'}
          >
            {pageNum}
          </Link>
        );
      })}
    </div>
  );
};

export default Paginate;