      <p>TKETC'nin cayma hakkn kulland durumlarda ya da siparie konu olan rnn eitli sebeplerle tedarik edilememesi veya hakem heyeti kararlar ile TKETC'ye bedel iadesine karar verilen durumlarda, deme seeneklerine ilikin iade prosedr, seilen deme yntemine gre ileyecektir...</p>
      {/* ade prosedrnn detaylar buraya eklenebilir. */}
      
