import React, { useState, useEffect, useContext } from 'react';
import { API_BASE_URL } from '../../config/api';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../../context/AuthContext';
import { toast } from 'react-toastify';
import './AdminTable.css'; 

function ProductTreeListPage() {
    const [productTrees, setProductTrees] = useState([]);
    const [loading, setLoading] = useState(true);
    // === YEN STATE'LER: Arama ve geniletme iin ===
    const [searchTerm, setSearchTerm] = useState('');
    const [filteredTrees, setFilteredTrees] = useState([]);
    const [expandedId, setExpandedId] = useState(null);

    const { authToken } = useContext(AuthContext);
    const navigate = useNavigate();

    useEffect(() => {
        const fetchTrees = async () => {
            try {
                setLoading(true);
                const res = await fetch(`${API_BASE_URL}/api/product-trees`, {
                    headers: { 'Authorization': `Bearer ${authToken}` }
                });
                const data = await res.json();
                if (!res.ok) throw new Error('rn aalar getirilemedi.');
                setProductTrees(data);
                setFilteredTrees(data); // Balangta tm aalar gster
            } catch (error) {
                toast.error(error.message);
            } finally {
                setLoading(false);
            }
        };
        if (authToken) {
            fetchTrees();
        }
    }, [authToken]);

    // === YEN FONKSYON: Arama terimi deitike listeyi filtreler ===
    useEffect(() => {
        const results = productTrees.filter(tree =>
            tree.name.toLowerCase().includes(searchTerm.toLowerCase())
        );
        setFilteredTrees(results);
    }, [searchTerm, productTrees]);

    const handleDelete = async (id) => {
        if (window.confirm('Bu rn aacn silmek istediinizden emin misiniz?')) {
            try {
                await fetch(`${API_BASE_URL}/api/product-trees/${id}`, {
                    method: 'DELETE',
                    headers: { 'Authorization': `Bearer ${authToken}` }
                });
                toast.success('rn aac silindi.');
                const updatedTrees = productTrees.filter(tree => tree._id !== id);
                setProductTrees(updatedTrees);
            } catch (error) {
                toast.error('Silme ilemi baarsz oldu.');
            }
        }
    };

    // === YEN FONKSYON: Detaylar ap kapatr ===
    const toggleDetails = (id) => {
        setExpandedId(expandedId === id ? null : id);
    };

    if (loading) return <div className="loading-container">Ykleniyor...</div>;

    return (
        <div className="admin-page-container">
            <div className="admin-page-header">
                <h1>rn Aalar (Reeteler)</h1>
                <button onClick={() => navigate('/admin/product-tree/new')} className="add-new-btn">Yeni rn Aac Olutur</button>
            </div>

            {/* === YEN BLM: Arama ubuu === */}
            <div className="search-form">
                <input
                    type="text"
                    placeholder="Reete ad ile ara..."
                    className="search-input"
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                />
            </div>

            <div className="admin-table-container">
                <table className="admin-table">
                    <thead>
                        <tr>
                            <th>Reete Ad</th>
                            <th>Para Says</th>
                            <th>lemler</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredTrees.map(tree => (
                            <React.Fragment key={tree._id}>
                                <tr className="clickable-row" onClick={() => toggleDetails(tree._id)}>
                                    <td>{tree.name}</td>
                                    <td>{tree.components.length} para</td>
                                    <td className="action-cell">
                                        <button onClick={(e) => { e.stopPropagation(); navigate(`/admin/product-tree/${tree._id}`); }} className="edit-btn">Dzenle</button>
                                        <button onClick={(e) => { e.stopPropagation(); handleDelete(tree._id); }} className="delete-btn">Sil</button>
                                    </td>
                                </tr>
                                {/* === YEN BLM: Geniletilebilir Detaylar === */}
                                {expandedId === tree._id && (
                                    <tr className="history-details-row">
                                        <td colSpan="3" className="history-details-cell">
                                            <div className="history-log">
                                                <h4>Reete erii</h4>
                                                {tree.components && tree.components.length > 0 ? (
                                                    <ul className="component-list">
                                                        {tree.components.map((comp, index) => (
                                                            <li key={index}>
                                                                <span className="component-name">{comp.inventoryItem.name}</span>
                                                                <span className="component-qty">{comp.quantity} adet</span>
                                                            </li>
                                                        ))}
                                                    </ul>
                                                ) : (
                                                    <p>Bu reeteye henz para eklenmemi.</p>
                                                )}
                                            </div>
                                        </td>
                                    </tr>
                                )}
                            </React.Fragment>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default ProductTreeListPage;