import React, { useState, useEffect, useContext } from 'react';
import { API_BASE_URL } from '../../config/api';
import { useParams, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../context/AuthContext';
import { toast } from 'react-toastify';
import './AdminForm.css';
import './ProductTreePage.css';

function ProductTreeEditPage() {
    const { id: treeId } = useParams();
    const navigate = useNavigate();
    const { authToken } = useContext(AuthContext);

    const [isNewTree, setIsNewTree] = useState(!treeId);
    const [inventoryItems, setInventoryItems] = useState([]);
    const [treeName, setTreeName] = useState('');
    const [components, setComponents] = useState([]);
    const [loading, setLoading] = useState(true);

    const [targetTerm, setTargetTerm] = useState(0);
    const [calculatedCost, setCalculatedCost] = useState(null);

    useEffect(() => {
        const fetchInitialData = async () => {
            try {
                const res = await fetch(`${API_BASE_URL}/api/inventory`, { headers: { 'Authorization': `Bearer ${authToken}` } });
                const data = await res.json();
                if (!res.ok) throw new Error('Stok kalemleri getirilemedi.');
                setInventoryItems(data);

                if (!isNewTree) {
                    const treeRes = await fetch(`${API_BASE_URL}/api/product-trees/${treeId}`, { headers: { 'Authorization': `Bearer ${authToken}` } });
                    const treeData = await treeRes.json();
                    if(!treeRes.ok) throw new Error('rn aac verisi alnamad.');
                    setTreeName(treeData.name);
                    const populatedComponents = treeData.components.map(comp => ({ ...comp.inventoryItem, quantity: comp.quantity }));
                    setComponents(populatedComponents);
                }
            } catch (error) { toast.error(error.message); } 
            finally { setLoading(false); }
        };
        if(authToken) fetchInitialData();
    }, [authToken, treeId, isNewTree]);

    const handleAddItem = (e) => {
        const selectedId = e.target.value;
        if (!selectedId) return;
        const itemToAdd = inventoryItems.find(i => i._id === selectedId);
        if (itemToAdd) {
            const existingItem = components.find(item => item._id === selectedId);
            if (existingItem) {
                const updatedComponents = components.map(item => item._id === selectedId ? {...item, quantity: (item.quantity || 1) + 1} : item);
                setComponents(updatedComponents);
            } else {
                setComponents([...components, { ...itemToAdd, quantity: 1 }]);
            }
        }
        e.target.value = "";
    };

    const handleQuantityChange = (id, quantity) => {
        const updatedComponents = components.map(item => 
            item._id === id ? {...item, quantity: parseInt(quantity) || 1} : item
        );
        setComponents(updatedComponents);
    };

    const handleRemoveItem = (id) => {
        setComponents(components.filter(item => item._id !== id));
    };

    const handleSaveTree = async () => {
        if (!treeName.trim()) return toast.error('Ltfen reete iin bir isim girin.');
        
        const componentData = components.map(c => ({ inventoryItem: c._id, quantity: c.quantity }));
        const url = isNewTree ? '/api/product-trees' : `/api/product-trees/${treeId}`;
        const method = isNewTree ? 'POST' : 'PUT';

        try {
            const res = await fetch(`${API_BASE_URL}${url}`, {
                method: method,
                headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${authToken}` },
                body: JSON.stringify({ name: treeName, components: componentData })
            });
            const data = await res.json();
            if (!res.ok) throw new Error(data.msg || 'Reete kaydedilemedi.');
            
            toast.success(`Reete baaryla ${isNewTree ? 'kaydedildi' : 'gncellendi'}.`);
            if (isNewTree) {
                navigate(`/admin/product-tree/${data._id}`, { replace: true });
            }
        } catch (error) {
            toast.error(error.message);
        }
    };
    
    const handleCalculateCost = async () => {
        if (components.length === 0) return toast.error('nce rn aacna para ekleyin.');
        const componentData = components.map(item => ({ inventoryItem: item._id, quantity: item.quantity }));
        try {
            const res = await fetch(`${API_BASE_URL}/api/costing/calculate`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${authToken}` },
                body: JSON.stringify({ components: componentData, targetTerm, targetCurrency: 'TL' })
            });
            const data = await res.json();
            if (!res.ok) throw new Error(data.msg || 'Maliyet hesaplanamad.');
            setCalculatedCost(data);
            toast.success('Maliyet baaryla hesapland!');
        } catch (error) {
            toast.error(error.message);
        }
    };
    
    const handleListProductForSale = () => {
        if (!treeName.trim() || isNewTree) {
            return toast.error('Ltfen nce bu reeteyi kaydedin.');
        }
        if (!calculatedCost) {
            return toast.error('Sata karmadan nce maliyeti hesaplamalsnz.');
        }

        const productToCreate = {
            name: treeName,
            costPrice: parseFloat(calculatedCost.totalCostTL),
            components: components.map(c => ({ inventoryItem: c._id, quantity: c.quantity }))
        };

        navigate('/admin/product/new', { state: { newProductFromTree: productToCreate } });
    };

    if (loading) return <div className="loading-container">Ykleniyor...</div>;

    return (
        <div className="admin-page-container">
            <button onClick={() => navigate('/admin/product-trees')} className="back-btn">&larr; rn Aac Listesine Dn</button>
            <h1>{isNewTree ? 'Yeni rn Aac Olutur' : 'rn Aacn Dzenle'}</h1>

            <div className="admin-form-container">
                <div className="form-group">
                    <label>rn Aac Ad (Reete Ad)</label>
                    <input type="text" value={treeName} onChange={e => setTreeName(e.target.value)} />
                </div>
            </div>

            <div className="product-tree-grid">
                <div className="tree-builder-container">
                    <h3>Para Ekle</h3>
                    <div className="add-item-form">
                        <select onChange={handleAddItem} defaultValue="">
                            <option value="" disabled>Stoktan Para Se...</option>
                            {inventoryItems.map(item => ( <option key={item._id} value={item._id}>{item.name} ({item.itemCode})</option> ))}
                        </select>
                    </div>
                    <div className="admin-table-container">
                        <table className="admin-table">
                            <thead><tr><th>Para Ad</th><th>Alm Tr</th><th>Adet</th><th>lem</th></tr></thead>
                            <tbody>
                                {components.map((item, index) => (
                                    <tr key={index}>
                                        <td>{item.name}</td>
                                        <td>{item.purchaseType === 'vadeli' ? `${item.termMonths || 0} Ay Vadeli` : 'Nakit'}</td>
                                        <td><input type="number" value={item.quantity} onChange={(e) => handleQuantityChange(item._id, e.target.value)} className="inline-input" /></td>
                                        <td><button onClick={() => handleRemoveItem(item._id)} className="delete-btn">Kaldr</button></td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>

                <div className="cost-calculator-container">
                    <h3>Maliyet ve Sat</h3>
                    <button onClick={handleSaveTree} className="submit-btn full-width" style={{marginBottom: '1rem', backgroundColor: '#ffc107', color: '#212529'}}>
                        {isNewTree ? 'Reeteyi Kaydet' : 'Deiiklikleri Kaydet'}
                    </button>
                    <hr className="divider" />
                    <div className="form-group">
                        <label>stenen Vade (Ay)</label>
                        <input type="number" value={targetTerm} onChange={e => setTargetTerm(parseInt(e.target.value) || 0)} />
                    </div>
                    <button onClick={handleCalculateCost} className="submit-btn full-width">Maliyeti Hesapla</button>
                    {calculatedCost && (
                        <div className="cost-result">
                            <h4>Hesaplanan Toplam Maliyet</h4>
                            <p className="cost-tl">{calculatedCost.totalCostTL} TL</p>
                        </div>
                    )}
                    <hr className="divider" />
                    <button onClick={handleListProductForSale} className="approve-btn full-width" disabled={isNewTree}>
                        Bu Reeteyi Sata kar
                    </button>
                </div>
            </div>
        </div>
    );
}

export default ProductTreeEditPage;