import React, { createContext, useState, useEffect } from 'react';
import { jwtDecode } from 'jwt-decode';
import { API_BASE_URL } from '../config/api';

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [authToken, setAuthToken] = useState(localStorage.getItem('token'));
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true); // Yklenme durumu iin yeni state

  useEffect(() => {
    // Uygulama her yklendiinde veya token deitiinde bu fonksiyon alr
    const initializeUser = () => {
      if (authToken) {
        try {
          const decodedToken = jwtDecode(authToken);
          
          // Token'n sresinin dolup dolmadn kontrol et
          const currentTime = Date.now() / 1000;
          if (decodedToken.exp < currentTime) {
            // Token'n sresi dolmusa, logout yap
            logout();
          } else {
            // Token geerliyse, kullanc bilgisini state'e ata
            setUser(decodedToken.user);
          }
        } catch (error) {
          // Token zlemezse (hatalysa), logout yap
          console.error("Geersiz token:", error);
          logout();
        }
      }
      setLoading(false); // Kontroller bitti, yklenme durumunu kapat
    };
    
    initializeUser();
  }, [authToken]);

  // Global fetch interceptor
  useEffect(() => {
    const originalFetch = window.fetch;
    window.fetch = async (input, init = {}) => {
      // Varsaylan olarak credentials dahil et
      const newInit = { ...init, credentials: 'include' };
      newInit.headers = {
        ...(init.headers || {}),
        ...(authToken ? { 'Authorization': `Bearer ${authToken}` } : {})
      };

      // URL rewrite
      let target = input;
      if (typeof target === 'string') {
        if (target.startsWith('http://localhost:5001')) {
          target = target.replace('http://localhost:5001', API_BASE_URL);
        } else if (target.startsWith('/')) {
          // URL'leri birletirirken oluabilecek ift /api veya // sorunlarn zen daha salam bir yap
          const combinedUrl = `${API_BASE_URL.replace(/\/$/, '')}${target}`;
          target = combinedUrl.replace('/api/api/', '/api/');
        }
      }

      let response = await originalFetch(target, newInit);
      if (response.status === 401) {
        // Access token sresi dolmu olabilir  refresh dene
        try {
          const refreshRes = await originalFetch(`${API_BASE_URL}/api/users/auth/refresh`, {
            method: 'POST',
            credentials: 'include'
          });
          if (refreshRes.ok) {
            const data = await refreshRes.json();
            login(data.token);
            // lk istei token ile tekrar yap
            newInit.headers['Authorization'] = `Bearer ${data.token}`;
            response = await originalFetch(target, newInit);
          } else {
            logout();
          }
        } catch (err) {
          console.error('Token yenileme hatas', err);
          logout();
        }
      }
      return response;
    };
    return () => {
      window.fetch = originalFetch;
    };
  }, [authToken]);

  const login = (token) => {
    localStorage.setItem('token', token);
    setAuthToken(token);
    // Token state'e atandnda useEffect tetiklenecek ve user bilgisi gncellenecek.
  };

  const logout = () => {
    localStorage.removeItem('token');
    setAuthToken(null);
    setUser(null);
  };

  const value = {
    authToken,
    user,
    loading, // AdminRoute'un kullanmas iin loading durumunu da export ediyoruz
    login,
    logout
  };

  // Yklenme tamamlanana kadar alt bileenleri (tm uygulamay) render etme
  // Bu, zamanlama sorunlarn tamamen zer.
  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};