import React, { useContext } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { AuthContext } from '../../context/AuthContext';

/**
 * SalesRepRoute Bileeni
 * * Bu bileen, ierisine ald sayfalar (children) sadece rol 'sales_rep' veya 'admin' olan
 * kullanclarn grebilmesini salar. Yetkisiz kullanclar ynlendirir.
 * * @param {object} props - React bileen props'lar
 * @param {React.ReactNode} props.children - Korumal rota iinde render edilecek olan bileenler
 */
function SalesRepRoute({ children }) {
  // AuthContext'ten kullanc bilgilerini ve yklenme durumunu alyoruz.
  const { user, authToken, loading } = useContext(AuthContext);
  const location = useLocation();

  // AuthContext token' kontrol ederken bekleme durumu.
  // Bu, sayfa ilk yklendiinde kullanc bilgisi gelmeden ynlendirme yaplmasn nler.
  if (loading) {
    return <div>Ykleniyor...</div>;
  }

  // Eer token veya kullanc bilgisi yoksa, giri sayfasna ynlendir.
  // Kullancnn gelmek istedii sayfay (location) state olarak gnderiyoruz ki,
  // giri yaptktan sonra kald yerden devam edebilsin.
  if (!authToken || !user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  // Eer kullancnn rol 'sales_rep' veya 'admin' deilse, ana sayfaya ynlendir.
  // Adminlerin de bu sayfalar grebilmesi test ve ynetim kolayl salar.
  if (user.role !== 'sales_rep' && user.role !== 'admin') {
    return <Navigate to="/" replace />;
  }

  // Tm kontrollerden geerse, istenen sayfay (children) gster.
  return children;
}

export default SalesRepRoute;