import React, { useState, useEffect, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../context/AuthContext';
import { toast } from 'react-toastify';
import './AdminForm.css';

function UserEditPage() {
  const { id: userId } = useParams();
  const navigate = useNavigate();
  const { authToken } = useContext(AuthContext);

  const [user, setUser] = useState(null);
  const [salesReps, setSalesReps] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        setLoading(true);
        // API isteklerini proxy zerinden yapyoruz, tam URL'ye gerek yok.
        const [userRes, repsRes] = await Promise.all([
            fetch(`/api/users/admin/${userId}`, { // Genel kullanc bilgisi
                headers: { 'Authorization': `Bearer ${authToken}` }
            }),
            fetch(`/api/admin/salesreps`, { // Pazarlamaclar admin rotasndan al
                headers: { 'Authorization': `Bearer ${authToken}` }
            })
        ]);
        
        const userData = await userRes.json();
        const repsData = await repsRes.json();

        if (!userRes.ok) throw new Error(userData.msg || 'Kullanc verisi alnamad.');
        if (!repsRes.ok) throw new Error(repsData.msg || 'Sat temsilcileri alnamad.');

        setUser(userData);
        setSalesReps(repsData);

      } catch (err) {
        setError(err.message);
        toast.error(err.message);
      } finally {
        setLoading(false);
      }
    };
    if (authToken) fetchInitialData();
  }, [userId, authToken]);

  const handleChange = (e) => {
    const { name, type, value, checked } = e.target;
    
    // Deeri alyoruz
    const newValue = type === 'checkbox' ? checked : value;

    // State'i gncellemek iin yeni bir obje oluturuyoruz
    const updatedUser = { ...user, [name]: newValue };

    // === YEN MANTIK BURADA ===
    // Eer deien alan "role" ise ve yeni rol "customer" deilse,
    // salesRepresentative alann temizliyoruz.
    if (name === 'role' && newValue !== 'customer') {
        updatedUser.salesRepresentative = null;
    }

    // "null" string'ini gerek null'a eviriyoruz
    if (name === "salesRepresentative" && newValue === "null") {
        updatedUser.salesRepresentative = null;
    }
    
    setUser(updatedUser);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Sat temsilcisi ID'sini al
      const salesRepresentative = user.salesRepresentative;

      // Gncelleme iin temiz payload olutur
      const userData = {};

      // Temel alanlar ekle
      if (user.name) userData.name = user.name;
      if (user.email) userData.email = user.email;
      if (user.role) {
        userData.role = user.role;
      }
      if (typeof user.isApproved === 'boolean') userData.isApproved = user.isApproved;
      if (user.companyTitle) userData.companyTitle = user.companyTitle;

      // Adres bilgisi varsa ve en az bir alan doluysa ekle
      if (user.addresses && user.addresses[0]) {
        const addr = user.addresses[0];
        const addressUpdates = {};
        if (addr.province) addressUpdates.province = addr.province;
        if (addr.district) addressUpdates.district = addr.district;
        if (addr.fullAddress) addressUpdates.fullAddress = addr.fullAddress;
        if (addr.addressTitle) addressUpdates.addressTitle = addr.addressTitle;
        if (addr._id) addressUpdates._id = addr._id;

        if (Object.keys(addressUpdates).length > 0) {
          userData.address = addressUpdates;
        }
      }
      
      // stek verisini logla
      console.log('Gnderilen kullanc verisi:', userData);
      console.log('Atanacak sat temsilcisi ID:', salesRepresentative);

      // 1. Adm: Temel kullanc bilgilerini gncelle
      const response = await fetch(`/api/users/admin/${userId}`, {
        method: 'PUT',
        headers: { 
          'Content-Type': 'application/json', 
          'Authorization': `Bearer ${authToken}` 
        },
        body: JSON.stringify(userData)
      });
      
      const data = await response.json();
      console.log('Kullanc gncelleme yant:', data);
      
      if (!response.ok) {
        throw new Error(data.msg || 'Genel kullanc bilgileri gncellenirken hata olutu.');
      }

      // 2. Adm: Eer kullanc bir mteriyse, sat temsilcisi atamasn yap
      if (user.role === 'customer') {
        console.log('Sat temsilcisi atanyor...');
        const assignResponse = await fetch(`/api/admin/users/${userId}/assign-salesrep`, {
          method: 'PUT',
          headers: { 
            'Content-Type': 'application/json', 
            'Authorization': `Bearer ${authToken}` 
          },
          body: JSON.stringify({ salesRepId: salesRepresentative || null })
        });

        const assignData = await assignResponse.json();
        console.log('Sat temsilcisi atama yant:', assignData);
        
        if (!assignResponse.ok) {
          throw new Error(assignData.msg || 'Pazarlamac atamas baarsz.');
        }
      }

      // Baarl olduunda kullancy bilgilendir ve listeye ynlendir
      toast.success('Kullanc baaryla gncellendi.');
      navigate('/admin/users');
    } catch (err) {
      console.error('Hata detay:', err);
      toast.error(err.message || 'Bir hata olutu. Ltfen tekrar deneyin.');
    }
  };

  if (loading) return <div className="loading-container">Ykleniyor...</div>;
  if (error) return <div className="error-container">Hata: {error}</div>;
  if (!user) return <div>Kullanc bulunamad.</div>;

  return (
    <div className="admin-page-container">
      <button onClick={() => navigate('/admin/users')} className="back-btn"> &larr; Mteri Listesine Dn</button>
      <h1>Mteriyi Dzenle: {user.name}</h1>
      <form onSubmit={handleSubmit} className="admin-form">
        <div className="form-grid">
            <div className="form-group">
                <label>Ad Soyad / Yetkili</label>
                <input type="text" name="name" value={user.name || ''} onChange={handleChange} />
            </div>
            {user.__t === 'CorporateUser' && (
                <div className="form-group">
                    <label>Firma nvan</label>
                    <input type="text" name="companyTitle" value={user.companyTitle || ''} onChange={handleChange} />
                </div>
            )}
            <div className="form-group">
                <label>Email</label>
                <input type="email" name="email" value={user.email || ''} onChange={handleChange} />
            </div>
            <div className="form-group">
                <label>Kullanc Rol</label>
                <select name="role" value={user.role} onChange={handleChange}>
                    <option value="customer">Mteri</option>
                    <option value="sales_rep">Sat Temsilcisi</option>
                    <option value="supplier">Tedariki</option>
                    <option value="admin">Admin</option>
                </select>
            </div>
            <div className="form-group">
                <label>Cari Hesap Bakiyesi ()</label>
                <input type="number" step="0.01" name="currentAccountBalance" value={user.currentAccountBalance} onChange={handleChange} />
            </div>
            
            {user.role === 'customer' && (
                <div className="form-group">
                    <label>Atanm Sat Temsilcisi</label>
                    <select name="salesRepresentative" value={user.salesRepresentative || "null"} onChange={handleChange}>
                        <option value="null">-- Temsilci Yok --</option>
                        {salesReps.map(rep => (
                            <option key={rep._id} value={rep._id}>{rep.name}</option>
                        ))}
                    </select>
                </div>
            )}
            <div className="form-group form-group-checkbox">
                <label>Onay Durumu</label>
                <input type="checkbox" name="isApproved" checked={user.isApproved} onChange={handleChange} /> Onayl
            </div>

        </div>
        <button type="submit" className="submit-btn">Deiiklikleri Kaydet</button>
      </form>
    </div>
  );
}

export default UserEditPage;