import React, { useState, useEffect, useContext, useMemo } from 'react';
import { API_BASE_URL, assetUrl } from '../../config/api';
import { useParams, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../context/AuthContext';
import { toast } from 'react-toastify';
import './OrderDetailPage.css'; // Stil dosyamz import ediyoruz

function OrderDetailPage() {
  const { id: orderId } = useParams();
  const navigate = useNavigate();
  const { authToken } = useContext(AuthContext);

  const [order, setOrder] = useState(null); // Dzenlenebilir sipari verisini tutan state
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isSaving, setIsSaving] = useState(false);

  // Sipari detaylarn getiren fonksiyon
  const enrichItemsWithImages = async (orderData) => {
    try {
      const allItems = [...orderData.orderItems, ...(orderData.backorderedItems || [])];
      const productsToFetch = allItems.filter(i => !(i.image || (i.images && i.images.length))).map(i => i.product);
      const uniqueIds = [...new Set(productsToFetch)];
      if(uniqueIds.length === 0) return;
      const responses = await Promise.all(uniqueIds.map(id => fetch(`/api/products/${id}`)));
      const products = await Promise.all(responses.map(r => r.json()));
      const imageMap = {};
      products.forEach(p=>{ imageMap[p._id] = p.images && p.images.length ? p.images[0] : p.image; });
      setOrder(prev=> ({
        ...prev,
        orderItems: prev.orderItems.map(it=> imageMap[it.product] ? {...it, image: imageMap[it.product]} : it),
        backorderedItems: prev.backorderedItems?.map(it=> imageMap[it.product] ? {...it, image: imageMap[it.product]} : it)
      }));
    } catch(err){ console.error('Resimler alnamad', err); }
  };

  const fetchOrderDetails = async () => {
    try {
      setLoading(true);
      const response = await fetch(`/api/orders/${orderId}`, {
        headers: { 'Authorization': `Bearer ${authToken}` }
      });
      const data = await response.json();
      if (!response.ok) throw new Error(data.msg || 'Sipari detaylar getirilemedi.');
      setOrder(data);
          // Eksik resimleri getir
          enrichItemsWithImages(data);
    } catch (err) {
      setError(err.message);
      toast.error(err.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (authToken && orderId) {
      fetchOrderDetails();
    }
  }, [authToken, orderId]);

  // Sipari kalemlerinin adedini deitiren fonksiyon
  const handleQuantityChange = (productId, newQty) => {
    const quantity = parseInt(newQty, 10);
    if (isNaN(quantity) || quantity < 0) return;

    setOrder(prevOrder => ({
      ...prevOrder,
      orderItems: prevOrder.orderItems.map(item => 
        item.product === productId ? { ...item, qty: quantity } : item
      )
    }));
  };

  // Bir kalemi sipariten tamamen silen fonksiyon
  const handleDeleteItem = (itemToDelete) => {
      if(window.confirm(`"${itemToDelete.name}" rnn sipariten tamamen kaldrmak istediinizden emin misiniz? Bu ilem geri alnamaz.`)){
        setOrder(prevOrder => ({
            ...prevOrder,
            orderItems: prevOrder.orderItems.filter(item => item.product !== itemToDelete.product)
        }));
      }
  };

  // Bir kalemi "Eksik rnler" listesine tayan fonksiyon
  const handleBackorderItem = (itemToBackorder) => {
    setOrder(prevOrder => ({
        ...prevOrder,
        orderItems: prevOrder.orderItems.filter(item => item.product !== itemToBackorder.product),
        backorderedItems: [...(prevOrder.backorderedItems || []), itemToBackorder]
    }));
  };

  // Eksik bir rn tekrar sipari kalemlerine ekleyen fonksiyon
  const handleFulfillBackorder = (itemToFulfill) => {
    setOrder(prevOrder => ({
        ...prevOrder,
        backorderedItems: prevOrder.backorderedItems.filter(item => item.product !== itemToFulfill.product),
        orderItems: [...prevOrder.orderItems, itemToFulfill]
    }));
  };

  // Yazdr ilevi
  const handlePrint = async () => {
    try {
      if (order.status === 'Beklemede') {
        const res = await fetch(`/api/orders/${orderId}/status`, {
          method: 'PATCH',
          headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${authToken}` },
          body: JSON.stringify({ status: 'Hazrlanyor' })
        });
        const data = await res.json();
        if (!res.ok) throw new Error(data.msg || 'Durum gncellenemedi');
        setOrder(data);
      }
    } catch (err) {
      toast.error(err.message);
    } finally {
      window.print();
    }
  };

  // Toplam tutar anlk olarak hesaplayan useMemo
  const currentTotal = useMemo(() => {
    if (!order) return 0;
    return order.orderItems.reduce((acc, item) => acc + item.price * item.qty, 0);
  }, [order]);
  
  // Deiiklikleri backend'e kaydeden fonksiyon
  const handleSaveChanges = async () => {
    setIsSaving(true);
    try {
        const payload = {
            orderItems: order.orderItems,
            backorderedItems: order.backorderedItems
        };
        const response = await fetch(`/api/orders/${orderId}`, {
            method: 'PUT',
            headers: {'Content-Type': 'application/json', 'Authorization': `Bearer ${authToken}`},
            body: JSON.stringify(payload)
        });
        const data = await response.json();
        if(!response.ok) throw new Error(data.msg || 'Sipari gncellenemedi.');

        toast.success('Sipari baaryla gncellendi!');
        fetchOrderDetails(); // Sayfay en gncel haliyle yeniden ek
    } catch (err) {
        toast.error(err.message);
    } finally {
        setIsSaving(false);
    }
  };


  if (loading) return <div className="loading-container">Sipari Detaylar Ykleniyor...</div>;
  if (error) return <div className="error-container">Hata: {error}</div>;
  if (!order) return <div>Sipari bulunamad.</div>;

  return (
    <div className="admin-page-container">
      <button onClick={() => navigate('/admin/orders')} className="back-btn">&larr; Tm Siparilere Dn</button>

      <div className="page-header">
        <h1>Sipari Dzenle (#{order._id.substring(0, 8)})</h1>
        <button onClick={handlePrint} className="print-btn">Yazdr</button>
      </div>

      <div className="print-header">
        <div className="print-header-left">
          <img src="/logo-print.png" alt="Curkuslar C.M.S Logo" className="print-logo" />
        </div>
        <div className="print-header-right">
          <p><strong>Sipari No:</strong> #{order._id.substring(0,8)}</p>
          <p><strong>Tarih:</strong> {new Date(order.createdAt).toLocaleString('tr-TR')}</p>
        </div>
      </div>
      <div className="order-detail-grid">
        <div className="order-detail-main">
          <div className="detail-card">
            <h3>Sipari erii</h3>
            <table className="order-items-table">
                <thead><tr><th>rn</th><th>Birim Fiyat</th><th>Adet</th><th>Toplam</th><th className="no-print">lemler</th><th className="print-only">Kontrol</th></tr></thead>
                <tbody>
                    {order.orderItems.map(item => (
                        <tr key={item.product}>
                            <td className="item-info"><img src={item.image ? assetUrl(item.image) : (item.images && item.images.length > 0 ? assetUrl(item.images[0]) : 'https://via.placeholder.com/60x60?text=No+Image')} alt={item.name} className="order-item-img" /> <span>{item.name}</span></td>
                            <td>{item.price.toFixed(2)} </td>
                            <td>
                                <input 
                                    type="number" 
                                    value={item.qty} 
                                    onChange={(e) => handleQuantityChange(item.product, e.target.value)}
                                    className="item-qty-input no-print"
                                />
                                <span className="print-only-inline">{item.qty}</span>
                            </td>
                            <td>{(item.price * item.qty).toFixed(2)} </td>
                            <td className="no-print">
                                <button title="Daha Sonra Gnder" className="action-btn backorder" onClick={() => handleBackorderItem(item)}><i className="fas fa-history"></i></button>
                                <button title="Sipariten Sil" className="action-btn delete" onClick={() => handleDeleteItem(item)}><i className="fas fa-trash"></i></button>
                            </td>
                            <td className="print-only"><span className="checkbox-box"></span></td>
                        </tr>
                    ))}
                </tbody>
            </table>
          </div>
          
          {order.backorderedItems && order.backorderedItems.length > 0 && (
            <div className="detail-card">
                <h3>Eksik rnler (Daha Sonra Gnderilecek)</h3>
                <table className="order-items-table">
                    <thead><tr><th>rn</th><th>Adet</th><th className="no-print">lem</th><th className="print-only">Kontrol</th></tr></thead>
                    <tbody>
                        {order.backorderedItems.map(item => (
                            <tr key={item.product}>
                                <td className="item-info"><img src={item.image ? assetUrl(item.image) : (item.images && item.images.length > 0 ? assetUrl(item.images[0]) : 'https://via.placeholder.com/60x60?text=No+Image')} alt={item.name} className="order-item-img" /> <span>{item.name}</span></td>
                                <td>{item.qty}</td>
                                <td className="no-print">
                                    <button title="Siparie Geri Ekle" className="action-btn" onClick={() => handleFulfillBackorder(item)}><i className="fas fa-undo"></i></button>
                                </td>
                                <td className="print-only"><span className="checkbox-box"></span></td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
          )}
        </div>
        <div className="order-detail-sidebar">
          <div className="detail-card no-print">
            <h3>Sipari zeti</h3>
            <div className="summary-row"><span>Orijinal Tutar:</span><strong>{order.originalTotalPrice.toFixed(2)} </strong></div>
            <div className="summary-row"><span>deme Yntemi:</span><strong>{order.paymentMethod}</strong></div>
            <div className="summary-row total">
              <span>GNCEL TOPLAM:</span>
              <strong>{currentTotal.toFixed(2)} </strong>
            </div>
            <button className="save-changes-btn" onClick={handleSaveChanges} disabled={isSaving}>
                {isSaving ? 'Kaydediliyor...' : 'Deiiklikleri Kaydet'}
            </button>
          </div>
          <div className="detail-card">
             <h3>Mteri Bilgileri</h3>
             <p><strong>sim:</strong> {order.user.name}</p>
             <p><strong>Email:</strong> {order.user.email}</p>
             {order.shippingAddress && (
                <div className="shipping-address">
                    <p><strong>Teslimat Adresi:</strong></p>
                    <p>{order.shippingAddress.fullAddress}, {order.shippingAddress.district}, {order.shippingAddress.province}</p>
                </div>
             )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default OrderDetailPage;