import React, { useState, useEffect, useContext } from 'react';
import { API_BASE_URL } from '../../config/api';
import { useParams, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../context/AuthContext';
import './AdminForm.css';

function CategoryEditPage() {
  const { id: categoryId } = useParams();
  const navigate = useNavigate();
  const { authToken } = useContext(AuthContext);

  const [name, setName] = useState('');
  // === YEN STATE'LER ===
  const [parent, setParent] = useState(null); // Seilen st kategoriyi tutar
  const [allCategories, setAllCategories] = useState([]); // st kategori dropdown' iin

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const isNewCategory = !categoryId;

  useEffect(() => {
    setLoading(true);
    const fetchInitialData = async () => {
      try {
        // Dropdown iin tm kategorileri ek
        const resAll = await fetch(`${API_BASE_URL}/api/categories`, {
            headers: { 'Authorization': `Bearer ${authToken}` }
        });
        const dataAll = await resAll.json();
        if(!resAll.ok) throw new Error("Kategoriler getirilemedi");
        setAllCategories(dataAll);

        // Eer dzenleme modundaysak, mevcut kategorinin bilgilerini ek
        if (!isNewCategory) {
          const resCurrent = await fetch(`${API_BASE_URL}/api/categories/${categoryId}`, {
              headers: { 'Authorization': `Bearer ${authToken}` }
          });
          const dataCurrent = await resCurrent.json();
          if (!resCurrent.ok) throw new Error("Kategori bulunamad");
          
          setName(dataCurrent.name);
          setParent(dataCurrent.parent || null); // Mevcut parent' ayarla
        }
      } catch(err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    
    if(authToken) {
        fetchInitialData();
    }
  }, [categoryId, isNewCategory, authToken]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(''); 
    setSuccess('');

    const url = isNewCategory ? `${API_BASE_URL}/api/categories` : `${API_BASE_URL}/api/categories/${categoryId}`;
    const method = isNewCategory ? 'POST' : 'PUT';

    // === GNCELLEME: Gnderilen veriye "parent" alan eklendi ===
    const bodyData = { name, parent: parent === "null" || parent === null ? null : parent };

    try {
        const response = await fetch(url, {
            method: method,
            headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${authToken}` },
            body: JSON.stringify(bodyData)
        });
        const data = await response.json();
        if (!response.ok) throw new Error(data.errors ? data.errors[0].msg : 'lem baarsz.');
        
        setSuccess(`Kategori baaryla ${isNewCategory ? 'oluturuldu' : 'gncellendi'}.`);
        
        if (isNewCategory) {
          setTimeout(() => navigate('/admin/categories'), 2000);
        }
    } catch (err) {
        setError(err.message);
    } finally {
        setLoading(false);
    }
  };
  
  if (loading && !allCategories.length) return <div className="loading-container">Ykleniyor...</div>;

  return (
    <div className="admin-page-container">
      <button onClick={() => navigate('/admin/categories')} className="back-btn"> &larr; Kategori Listesine Dn</button>
      <h1>{isNewCategory ? 'Yeni Kategori Ekle' : 'Kategoriyi Dzenle'}</h1>
      <form onSubmit={handleSubmit} className="admin-form" style={{maxWidth: '600px', margin: '2rem auto'}}>
        {success && <p className="success-message">{success}</p>}
        {error && !success && <p className="error-message">{error}</p>}
        
        <div className="form-group">
            <label htmlFor="name">Kategori Ad</label>
            <input type="text" id="name" name="name" value={name} onChange={(e) => setName(e.target.value)} required />
        </div>

        {/* === YEN ALAN: st Kategori Seimi === */}
        <div className="form-group">
            <label htmlFor="parent">st Kategori (Bo brakrsanz ana kategori olur)</label>
            <select id="parent" name="parent" value={parent || "null"} onChange={(e) => setParent(e.target.value)}>
                <option value="null">-- Ana Kategori --</option>
                {allCategories.map(cat => (
                    // Bir kategoriyi kendi kendisinin st kategorisi yapmasn engelle
                    cat._id !== categoryId && <option key={cat._id} value={cat._id}>{cat.name}</option>
                ))}
            </select>
        </div>

        <button type="submit" className="submit-btn" disabled={loading}>
            {loading ? 'Kaydediliyor...' : (isNewCategory ? 'Kategoriyi Olutur' : 'Deiiklikleri Kaydet')}
        </button>
      </form>
    </div>
  );
}

export default CategoryEditPage;